import { DeepLinkOptions } from '@utorgapp/deeplinks-generator'

export const formatOptions = (options: DeepLinkOptions) => {
	return Object.entries(options).reduce<DeepLinkOptions>(
		(acc, [key, option]) => {
			let val = option
			if (typeof val === 'string') {
				val = val.trim()
			}
			return { ...acc, [key]: val }
		},
		{} as DeepLinkOptions
	)
}
