import React, { useState, useEffect } from "react";
import {createWidget} from "@utorgapp/widget";

import './Widget.css'

export const Widget = ({controlledOptions}) => {
  const [widget, setWidget] = useState('');

  useEffect(() => {
    if (Object.keys(controlledOptions).length) {
      createWidget({ options: controlledOptions }).then(w => {
        setWidget(w)
      }).catch(e => {
        console.error('widget error', e);
      })
    }
  }, [controlledOptions])

  return (
    <div className="widget-wrapper" dangerouslySetInnerHTML={{ __html: widget }}></div>
  )
}
