import React, { useState } from "react";
import {
  Form,
  Button,
  Schema,
  FlexboxGrid,
  SelectPicker,
  ButtonToolbar,
} from "rsuite";

import './styles.css';
import './form.less';

const { StringType, NumberType } = Schema.Types;

const PAYMENT_TYPE = [
  { label: 'Fiat', value: 'fiat' },
  { label: 'Crypto', value: 'crypto' },
]

const model = Schema.Model({
  amount: NumberType(),
  paymentAmount: NumberType(),
  address: StringType().isRequired('Address is required'),
  externalId: StringType().isRequired('External Id is required'),
  merchantId: StringType().isRequired('Merchant Id is required'),
  paymentType: StringType().isRequired('Payment Type is required'),
  currency: StringType().isRequired('Currency is required'),
  chain: StringType().isRequired('Chain is required'),
  email: StringType().isEmail('Please enter a valid email address.'),
});

const TextField = React.forwardRef((props, ref) => {
  const { name, label, accepter, ...rest } = props;
  return (
    <Form.Group controlId={`${name}-4`} ref={ref}>
      <Form.ControlLabel>{label} </Form.ControlLabel>
      <Form.Control name={name} accepter={accepter} {...rest} />
    </Form.Group>
  );
});

const Field = React.forwardRef((props, ref) => {
  const { name, label, accepter, error, ...rest } = props;
  return (
    <Form.Group controlId={`${name}-10`} ref={ref} className={error ? 'has-error' : ''}>
      <Form.ControlLabel>{label} </Form.ControlLabel>
      <Form.Control
        name={name}
        accepter={accepter}
        errorMessage={error}
        className="widget__form-control"
        {...rest}
      />
    </Form.Group>
  );
});

export const WidgetForm = ({onSubmit}) => {
  const formRef = React.useRef();
  const [formError, setFormError] = React.useState({});
  const [selectedPaymentType, setSelectedPaymentType] = useState(PAYMENT_TYPE[0].value)
  const [formValue, setFormValue] = React.useState({
    amount: 100,
    paymentAmount: 200,
    address: 'address',
    externalId: 'some id',
    email: '',
    merchantId: 'some id',
    postbackUrl: '',
    paymentMethodId: '',
    chain: 'BITCOIN',
    currency: 'BTC',
    paymentCurrency: 'EUR',
    paymentType: PAYMENT_TYPE[0].value
  });

  React.useEffect(() => {
    onSubmit(formValue);
  }, [])

  const handleSubmit = () => {
    if (!formRef.current.check()) {
      console.error('Form Error', formError);
      return;
    }

    const {amount, paymentAmount, ...rest} = formValue;

    if (selectedPaymentType === 'crypto') {
      onSubmit({ amount: Number(amount), ...rest })
    } else {
      onSubmit({ paymentAmount: Number(paymentAmount), ...rest })
    }
  };

  return (
    <FlexboxGrid className="widget__grid">
      <FlexboxGrid.Item colspan={16}>
        <Form
          ref={formRef}
          onChange={setFormValue}
          onCheck={setFormError}
          formValue={formValue}
          model={model}
        >
          <Field
            name="paymentType"
            label="Payment Type"
            accepter={SelectPicker}
            error={formError.status}
            data={PAYMENT_TYPE}
            onChange={(type) => setSelectedPaymentType(type)}
          />
          {selectedPaymentType === 'crypto' ? (
            <TextField name="amount" label="Amount" />
          ) : (
            <TextField name="paymentAmount" label="Payment Amount" />
          )}
          <TextField name="address" label="Address" />
          <TextField name="externalId" label="External id" />
          <TextField name="email" label="Email" />
          <TextField name="merchantId" label="Merchant id" />
          <TextField name="postbackUrl" label="Postback url" />
          <TextField name="paymentMethodId" label="Payment Methods id" />

          <TextField name="chain" label="Chain" />
          <TextField name="currency" label="Currency" />
          <TextField name="paymentCurrency" label="Payment Currency" />

          <ButtonToolbar className="widget__button-toolbar">
            <Button block appearance="primary" onClick={handleSubmit}>
              Submit
            </Button>
          </ButtonToolbar>
        </Form>
      </FlexboxGrid.Item>
    </FlexboxGrid>
  );
};
